// menu
$('.header-btn').on('click', function() {
  $('.menu, .header__burger, .header-btn').toggleClass('active');


  if ($('.menu').hasClass('active')) {
    $('body').addClass('fixedPosition');
  }
  else {
    $('body').removeClass('fixedPosition');
  }



});


$('.menu__list').on('click', 'a', function(event) {
  event.preventDefault();
  var id = $(this).attr('href'),
    top = $(id).offset().top;
  $('body,html').animate({ scrollTop: top }, 1500);
});




// scroll


$(window).scroll(function() {
  var height = $(window).scrollTop();

  /*    100px     header*/
  if (height > 100) {
    $('header').addClass('header-fixed');
  } else {
    /*  100px    header*/
    $('header').removeClass('header-fixed');
  }


  if ($('header').hasClass('header-fixed')) {

    $('.scroll-fixed').addClass('fixed');
  } else {
    $('.scroll-fixed').removeClass('fixed');
  }

  if ($('header').hasClass('header-fixed')) {
    $('.os-scrollbar-horizontal').addClass('active');
    $('.model-fixed-text').addClass('active');
    $('.catalog-seleted-left-fixed').addClass('active');
    $('.catalog-seleted-right-fixed').addClass('active');
    $('.select-compare ').addClass('active');

  } else {
    $('.os-scrollbar-horizontal').removeClass('active');
    $('.catalog-seleted-left-fixed').removeClass('active');
    $('.catalog-seleted-right-fixed').removeClass('active');
    $('.select-compare ').removeClass('active');
    $('.model-fixed-text').removeClass('active');
  }


});








// hover
$('.bike1322').hover(function() {
  $('.drop__img').addClass('active');
}, function() {
  $('.drop__img').removeClass('active');
});



